<template>

<v-card
        flat
        tile
>
    <v-toolbar dense>
            <v-app-bar-nav-icon></v-app-bar-nav-icon>

            <v-toolbar-title @click="showPage = [true, false, false]">Интерфейс оператора</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-title @click="showPage = [false, true,  false]">Статистика линии</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-title @click="choosePage(2)">Закрытие смены</v-toolbar-title>

            <v-spacer></v-spacer>

    </v-toolbar>
    
    <!--Контейнер интерфейса оператора-->
    <v-container v-if="showPage[0]">
        <h1>Линия BR-92</h1>
        <h3>Участок съемка</h3>
        
        
        <v-btn block v-if="showBlock === 0" @click="showBlock++">
            выполнить переход
        </v-btn>
        
        <!-- Выбор модели-->
        <v-container v-if="showBlock === 1">
            <v-card
                class="overflow-hidden"
                color="grey"
                dark
            >
                <v-toolbar
                flat
                color="black"
                >
                <v-toolbar-title class="font-weight-light">
                    Выбор модели
                </v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                <v-autocomplete
                    :items="models"
                    color="white"
                    item-text="model"
                    label="Модель"
                    item-value="id"
                    v-model = "formBase.id"
                ></v-autocomplete>
                <v-autocomplete
                    :items="models[formBase.id-1].articles"
                    color="white"
                    label="Артикль"
                    v-model="formBase.article"
                ></v-autocomplete>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    
                    color="success"
                    @click="chooseModel()"
                >
                    Save
                </v-btn>
                </v-card-actions>
                <v-snackbar
                v-model="hasSaved"
                :timeout="2000"
                absolute
                bottom
                left
                >
                Your profile has been updated
                </v-snackbar>
            </v-card>
        </v-container>

        <!-- Интерфейс счетчика дефетов-->
        <v-container v-if="showBlock === 2">
            <h3><div>Модель:{{formBase.model}} Артикль: {{formBase.article}}</div>
            <div>Всего стекла: {{workbase[workbase.length-1].counter.good.counter + workbase[workbase.length-1].counter.gap.counter + workbase[workbase.length-1].counter.printDefect.counter + workbase[workbase.length-1].counter.edgePrint.counter + workbase[workbase.length-1].counter.point.counter + workbase[workbase.length-1].counter.edgeDefect.counter + workbase[workbase.length-1].counter.breake.counter + workbase[workbase.length-1].counter.skol.counter + workbase[workbase.length-1].counter.scratch.counter}}</div>
            Статус стекла и дефекты</h3>
            <v-divider class="v-step"></v-divider>
            <v-row>
                <v-col cols="3">
                    <v-btn large block color="green" @click="workbase[workbase.length-1].counter.good.counter++">Годное</v-btn>
                </v-col>
            </v-row>
            <v-divider class="v-step"></v-divider>
            <v-row>
                <v-col cols="3">
                    <v-btn large block color="yellow" @click="workbase[workbase.length-1].counter.printDefect.counter++">Непрокрасс</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="yellow" @click="workbase[workbase.length-1].counter.edgePrint.counter++">Затек</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="yellow" @click="workbase[workbase.length-1].counter.point.counter++">Грязь</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="yellow" @click="workbase[workbase.length-1].counter.gap.counter++">GAP</v-btn>
                </v-col>
            </v-row>
            
            <v-divider class="v-step"></v-divider>
            <v-row>
                <v-col cols="3">
                    <v-btn large block color="red" @click="workbase[workbase.length-1].counter.edgeDefect.counter++">Обработка кромки</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="red" @click="workbase[workbase.length-1].counter.breake.counter++">Бой</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="red" @click="workbase[workbase.length-1].counter.skol.counter++">Скол</v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn large block color="red" @click="workbase[workbase.length-1].counter.scratch.counter++">Царапина</v-btn>
                </v-col>
            </v-row>
            <v-container>
                <v-btn block @click="endWork()">
                    закончить наработку
                </v-btn>
            </v-container>
        </v-container>
    </v-container>
    
    <!-- Контейнер визуализации показателей линии-->
    <v-container v-if="showPage[1]">
        <h1>Показатели линии BR-92</h1>
        <h3>Общие данные с наработки</h3>
        <v-simple-table>
            <template v-slot:default>
            <thead>
                <tr>
                <th class="text-left">
                    Дата
                </th>
                <th class="text-left">
                    Модель
                </th>
                <th class="text-left">
                    Артикль
                </th>
                <th class="text-left">
                    Годное
                </th>
                <th class="text-left">
                    НП
                </th>
                <th class="text-left">
                    Брак
                </th>
                <th class="text-left">
                    NRFT
                </th>
                <th class="text-left">
                    YIELD
                </th>
                <th class="text-left">
                    Основной дефект(количество)
                </th>
                </tr>
            </thead>
            <tbody>
                <tr
                v-for="item in workbaseState"
                :key="item.date"
                >
                <td>{{ formatDate(item.date) }}</td>
                <td>{{ item.model }}</td>
                <td>{{ item.article }}</td>
                <td>{{ item.counter.good.counter}}</td>
                <td>{{ item.counter.printDefect.counter + item.counter.edgePrint.counter+item.counter.point.counter+item.counter.gap.counter }}</td>
                <td>{{ item.counter.edgeDefect.counter + item.counter.breake.counter+item.counter.skol.counter+item.counter.scratch.counter }}</td>
                <td>{{ Math.round((item.counter.good.counter / (item.counter.good.counter+item.counter.printDefect.counter + item.counter.edgePrint.counter+item.counter.point.counter+item.counter.gap.counter+item.counter.edgeDefect.counter + item.counter.breake.counter+item.counter.skol.counter+item.counter.scratch.counter))*100) + "%"  }}</td>
                <td>{{ Math.round(((item.counter.good.counter + item.counter.printDefect.counter + item.counter.edgePrint.counter+item.counter.point.counter+item.counter.gap.counter) / (item.counter.good.counter+item.counter.printDefect.counter + item.counter.edgePrint.counter+item.counter.point.counter+item.counter.gap.counter+item.counter.edgeDefect.counter + item.counter.breake.counter+item.counter.skol.counter+item.counter.scratch.counter))*100) + "%"  }}</td>
                <td>{{ maxValue(item.counter)}}</td>
                </tr>
            </tbody>
            </template>
        </v-simple-table>
        <v-container>
            <h3 style="margin-bottom: 15px">TOP дефектов за сутки</h3>
            <v-card>
            <v-row >
                <v-col v-for="(item, ind) in statGraphMain" :key="ind">
                     <div>{{item.counter}}</div>
                     <div :style="{height: 100-100*item.height+'px'}" ></div>
                     <div :style="{height: 100*item.height+'px', background: 'green', width: '30px'}" ></div>
                     <div>{{item.name}}</div>
                </v-col>
            </v-row>
            </v-card>
        </v-container>
    </v-container>
</v-card>


</template>

<script>
    import models from "@/data/models.js"

    export default{
        data(){
            return{
                showBlock: 0,
                formBase: {
                    id: 1
                },
                workbase: [],
                showPage: [true, false]
            }
        },
        methods:{
            chooseModel(){
                this.showBlock = 2
                let obj = {}
                obj.model = this.models[this.formBase.id-1].model
                obj.counter = { 
                    good: {counter: 0, name: "годное"},
                    gap: {counter: 0, name: "GAP"},
                    printDefect: {counter: 0, name: "непропечатка"},
                    edgePrint: {counter: 0, name: "затеки"},
                    point: {counter: 0, name: "грязь"},
                    edgeDefect: {counter: 0, name: "Дефект кромки"},
                    breake: {counter: 0, name: "бой"},
                    skol:  {counter: 0, name: "скол"},
                    scratch: {counter: 0, name: "царапины"},   
                }
                obj.date = new Date()
                this.workbase.push(obj)

                
            },
            endWork(){
                this.showBlock = 0
                this.formBase.id = 1
            },
            maxValue(value){
                let max={ counter: 0, name: "дефектов" }
                if(max.counter < value.edgeDefect.counter){
                   max = value.edgeDefect 
                } 
                if (max.counter < value.breake.counter) {
                    max = value.breake
                }
                if (max.counter < value.skol.counter){
                    max =  value.skol
                }
                if (max.counter < value.scratch.counter){
                    max = value.scratch
                }
                return max.name+ " - " + max.counter
            },
            formatDate(value){
                let format = new Intl.DateTimeFormat("ru")
                return format.format(value)
            },
            choosePage(val){
                this.showPage.forEach(item => {item = false})
                this.showPage[val]=true
                
            }
        },
        computed: {
            models(){
                return models
            },
            workbaseState(){
                return this.workbase
            },
            statGraphMain(){
                let counter = {
                    gap: {counter: 0, name: "GAP"},
                    printDefect: {counter: 0, name: "непропечатка"},
                    edgePrint: {counter: 0, name: "затеки"},
                    point: {counter: 0, name: "грязь"},
                    edgeDefect: {counter: 0, name: "Дефект кромки"},
                    breake: {counter: 0, name: "бой"},
                    skol:  {counter: 0, name: "скол"},
                    scratch: {counter: 0, name: "царапины"},
                }
                this.workbase.forEach(elem => {
                    counter.gap.counter += elem.counter.gap.counter
                    counter.printDefect.counter += elem.counter.printDefect.counter
                    counter.edgePrint.counter += elem.counter.edgePrint.counter
                    counter.point.counter += elem.counter.point.counter
                    counter.edgeDefect.counter += elem.counter.edgeDefect.counter
                    counter.breake.counter += elem.counter.breake.counter
                    counter.skol.counter += elem.counter.skol.counter
                    counter.scratch.counter += elem.counter.scratch.counter
                })
                let stateGraph = []
                stateGraph.push(counter.gap)
                stateGraph.push(counter.printDefect)
                stateGraph.push(counter.edgePrint)
                stateGraph.push(counter.point)
                stateGraph.push(counter.edgeDefect)
                stateGraph.push(counter.breake)
                stateGraph.push(counter.skol)
                stateGraph.push(counter.scratch) 
                
                stateGraph.sort((a ,b) => b.counter - a.counter)
                stateGraph.forEach(item => {
                    console.log(item)
                    item.height = item.counter/stateGraph[0].counter
                })
                console.log(stateGraph)
                return stateGraph
            }   
        }
    }
</script>

<style>
    .v-step{
        margin: 10px 0 10px 0;
    }
</style>